import React, { Component } from 'react';
import connectionService from '../../Services/ConnectionService';
import ErrorMessage from '../../config/Message';
import Collapsible from '../../components/common/Collapsible';
import Title from '../../config/titles';

class NasInfo extends Component {
    constructor(props) {
        super(props);

        this.state = {
            NassInfoData: []
        };
        document.title = Title.NassInfo;
    }
    componentDidMount() {
        fetch(connectionService.NasInfo, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
            .then((response) => response.json())
            .then((responseJson) => {
                if (responseJson.success) {
                    var NassInfoData = responseJson.NasInfo;
                    NassInfoData.map((group, groupIndex) => {
                        group.expanded = false;
                    });

                    this.setState({ NassInfoData });
                }
                else {
                    alert(ErrorMessage.ServerError);
                }
            })
            .catch((error) => {
                console.error(error);
            });
    }

    onExpandToggle(field) {
        var expand = field.expanded;
        this.state.NassInfoData.map((group, groupIndex) => {
            group.expanded = false;
        });
        field.expanded = !expand;
        this.setState({ NassInfoData: this.state.NassInfoData });
    }

    render() {
        var tabs = { MainHeaderColumn: "Name", tabItems: [{ Title: "Overview", DataField: "Overview" }, { Title: "Reference", DataField: "Reference" }] };

        return (
            <div className="wrapper wrapper-content">
                <div className="row">
                    <div className="ibox float-e-margins">
                        <div className="ibox-title">
                            <h1>Nas Information</h1>
                            <Collapsible items={this.state.NassInfoData} tabs={tabs} onExpandToggle={this.onExpandToggle.bind(this)} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default NasInfo


